<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader :title="headercontent.title" />
        </v-col>

        <v-col md="12">
          <v-simple-table class="table" v-if="!loading">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">S/N</th>
                  <th class="text-left">Title</th>
                  <th class="text-left">Description</th>
                  <th class="text-left">User</th>
                  <th class="text-left">Action</th>
                </tr>
              </thead>
              <tbody v-if="allReports.results">
                <tr v-for="(item, index) in allReports.results" :key="index">
                  <td>{{ index + 1 }}</td>
                  <td class="text-capitalize">
                    {{ item.title}}
                  </td>
                  <td>{{ item.description }}</td>
                  <td class="text-capitalize">{{ item.userHandle }}</td>
                  <td>
                    <v-btn class="btn-dark" small @click="viewDetails(item._id)"
                      >View</v-btn
                    >
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div v-if="loading">
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
          </div>
          <ul
            class="pagination-button"
            v-if="allReports.results != undefined && !loading"
          >
            <li>
              Page :
              <span v-if="allReports.pagination.next">{{
                (allReports.pagination.next - 1) * this.limit
              }}</span
              ><span v-if="!allReports.pagination.next">{{
                allReports.pagination.total
              }}</span>
              -
              {{ allReports.pagination.total }}
            </li>
            <li>
              <div>
                <v-btn
                  v-if="allReports.pagination.previous"
                  @click="prevPage(allReports.pagination.previous)"
                  class="btn-dark"
                  >Prev</v-btn
                >

                <v-btn
                  v-if="allReports.pagination.next"
                  @click="nextPage(allReports.pagination.next)"
                  class="btn-dark"
                  >Next</v-btn
                >
              </div>
            </li>
          </ul>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Report",
  components: { PageHeader },
  data: () => ({
    headercontent: {
      title: "Report List",
      addlink: "/dashboard/user/add",
    },
    title: "",
    adsposition: "",
    valid: true,
    limit: 100,
    skip: 1,
    filter: {},
    loading: true,
  }),
  computed: {
    ...mapGetters(["allReports"]),
  },
  methods: {
    ...mapActions(["getReports"]),
    validate() {
      this.filter.role = this.role;
      this.getUsers({ limit: this.limit, skip: this.skip, role: this.role });
    },
    nextPage(data) {
      this.getReports({
        limit: this.limit,
        skip: data,
      });
    },
    prevPage(data) {
      this.getReports({
        limit: this.limit,
        skip: data,
      });
    },
  },
  async created() {
    await this.getReports({ limit: this.limit, skip: this.skip });
    this.loading = false;
  },
};
</script>

